.dates {
	padding: 76px 0 178px;
	@include tablet {
		padding: 85px 0; }
	@include md {
		padding: 99px 0 77px; }
	&__content {
		display: flex;
		justify-content: center;
		max-width: 1063px;
		margin: 0 auto;
		gap: 166px;
		@include tablet {
			max-width: 800px;
			gap: 60px; }
		@include md {
			gap: 40px;
			flex-wrap: wrap; }
		@include sm {
			padding: 0 15px; }

		&.compact {
			gap: 80px;
			@include tablet {
				gap: 40px; }

			.dates__icon {
				right: -66px; } } }

	&__item {
		display: flex;
		position: relative;
		@include sm {
			justify-content: center; }

		&:not(:last-child) {

			.dates__icon {
				display: flex;
				@include tablet {
					display: none; } } } }

	&__icon {
		display: none;
		position: absolute;
		font-size: 36px;
		right: -108px;
		top: 50%;
		transform: translate(-50%, -17px);

		.icon {
			fill: #ffffff; } } }

.date {
	color: #ffffff;
	position: relative;
	display: flex;
	text-decoration: none;
	@include hover {
		&:before {
			transform: translate(6px, -14px);
			width: calc(100% + 8px);
			height: calc(100% + 8px);
			@include sm {
				width: calc(100% + -12px); } }
		.date {
			&__name {
				text-decoration: none; }
			&__inner {
				transform: translate(-10px, 10px); }
			&__bg {
				transform: scale(1.15); } } }

	&.js-scroll-to {
		.date {
			&__name {
				text-decoration: underline; } } }

	@include sm {

		//width: 100%
		padding-left: 20px; }
	&:before, &:after {
		position: absolute;
		content: '';
		display: block;
		border: 4px solid #ffffff;
		background-color: $themeColor; }
	&:before {
		top: 20px;
		left: -20px;
		width: 100%;
		height: 100%;
		transform: translate(0, 0);
		transition: all .3s ease;
		@include sm {
			left: 0;
			width: calc(100% - 20px); } }
	&:after {
		top: 10px;
		left: -10px;
		width: 100%;
		height: 100%;
		@include sm {
			left: 10px;
			width: calc(100% - 20px); } }

	&__bg {
		object-fit: cover;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		transition: transform 0.3s ease;
		filter: brightness(0.5); }

	&__header {
		color: $white;
		position: relative;
		@include tablet {
			margin-bottom: 4px; }
		@include sm {
			// display: flex
			// flex-direction: row-reverse
			// justify-content: flex-end
 } }			// align-items: baseline
	&__title {
		font-size: 16px;
		min-height: 44px;
		display: flex;
		justify-content: space-between;
		@include tablet {
			font-size: 16px; }
		&:not(:last-child) {
			margin-bottom: 14px; } }
	&__inner {
		overflow: hidden;
		position: relative;
		z-index: 2;
		background-color: $white;
		padding: 14px 24px 24px;
		width: 237px;
		max-width: 100%;
		background-size: cover;
		transition: all .3s ease;
		transform: translate(0, 0);

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: #f00;
			z-index: -1;
			transition: all .3s ease; }

		@include tablet {
			width: 218px;
			padding: 19px 13px 18px; }
		@include r(820) {
			width: 217px; }
		@include sm {
			width: 270px;
			padding: 24px 21px 21px 19px; }
		@include xs {
			padding: 18px 21px 21px 19px; } }
	&__date {
		@include gotB;
		font-size: 44px;
		letter-spacing: 0.4px;
		color: inherit;
		text-decoration: none;
		position: relative;
		@include tablet {
			font-size: 40px; }
		@include sm {
			font-size: 40px;
			margin-right: 10px;
			line-height: 1; } }
	&__name {
		color: $white;
		font-size: 22px;
		line-height: 1.4;
		display: block;
		position: relative;
		@include tablet {
			font-size: 20px; }
		@include md {
			text-align: left; }
		@include sm {
			font-size: 24px; }
		&:not(:last-child) {
			margin: 17px 0 18px;
			@include tablet {
				margin-bottom: 15px; }
			@include md {
				margin-bottom: 13px; } } }
	&__info {
		text-align: left;
		position: relative;
		padding: 0;
		color: $white;
		@include md {
			padding: 0 10px 0 0; }

		a {
			color: $white; } } }





