.remote-switch {
  font-size: 12px;
  position: fixed;
  left: 50%;
  transform: translateX(-660px);
  top: 0;
  display: flex;
  height: 50px;
  align-items: center;
  padding: 0;
  background: $themeColor2;
  z-index: 11;
  pointer-events: auto;
  border: 2px solid $themeColor2;

  @include laptop {
    left: 50px;
    transform: translateX(0); }

  @include md {
    height: 38px;
    left: 0; }

  &__text {
    opacity: 0.8;
    text-decoration: none;
    color: $black;
    font-weight: 500;
    height: 100%;
    padding: 0 10px;
    display: flex;
    align-items: center;

    @include md {
      font-size: 10px; }

    &:first-child {
      padding: 0 10px 0 20px;

      @include md {
        padding: 0 10px 0 10px; } }

    &:last-child {
      padding: 0 20px 0 10px;

      @include md {
        padding: 0 10px 0 10px; } }


    &:not(.active):hover {
      color: $black;
      opacity: 1; }

    &.active {
      opacity: 1; } }

  &__switch {
    position: relative;
    width: 36px;
    height: 12px;
    border-radius: 6px;
    background: rgba(0, 0, 0, 0.2);
    cursor: pointer;

    @include md {
      width: 22px;
      height: 10px; }

    &:after {
      content: '';
      position: absolute;
      left: -3px;
      top: -3px;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background: $black;

      @include md {
        width: 14px;
        height: 14px;
        top: -2px;
        left: -2px; } }

    &.active {
      &:after {
        left: auto;
        right: -3px;

        @include md {
          left: auto;
          right: -2px; } } } } }
