.speakers {
  background: $grayL;
  padding: 145px 0 17px;

  .container {
    max-width: 1542px; }
  @include sm {
    padding-top: 53px; }
  &_pt-sm {
    padding-top: 94px;
    @include laptop {
      padding: 80px 0 10px; } }
  &_center {
    .speakers-list {
      justify-content: center;
      @include xs {
        justify-content: flex-start;
        //flex-direction: column
 } } }        //align-items: center
  &.no-tags {
    .speaker__tag {
      display: none; } }

  @include laptop {
    padding: 80px 0 10px; }
  //&_mb-sm
  //.speakers-list
  //  &__item
  //    &:last-child
  //      @include xs
  //        margin-bottom: 30px
  &__title {
    color: $themeColor;
    max-width: 1280px;
    margin: 0 auto; }
  &__btn {
    max-width: 283px;
    margin: 0 auto; } }

.speakers-list {
  display: flex;
  flex-wrap: wrap;
  margin: 60px -19px 0;
  justify-content: center;

  @include md {
    margin-top: 40px; }

  @include sm {
    margin-top: 20px; }

  &__item {
    width: 20%;
    padding: 0;
    margin-bottom: 40px;
    @include r(1440) {
      width: 25%; }
    @include tablet {
      width: 25%;
      margin-bottom: 38px; }
    @include md {
      width: 33.33%;
 }      //margin-bottom: 30px
    @include sm {
      width: 50%;
 }      //margin-bottom: 30px
    @include xs {
      padding: 0;
      //width: 100%
      //max-width: 250px
 }      //margin: 0 auto 48px

    &_cfp {
      .speaker {
        height: 100%;

        &__body {
          margin-top: 0;
          height: 100%;
          display: flex; }
        &__inner {
          padding: 20px;
          justify-content: center; }

        &__name {
          font-size: 36px;
          font-weight: 800;
          text-align: center; }

        &__company {
          font-weight: 500; } }

      .btn {
        min-height: 72px;
        padding: 5px 10px;
        background: $themeColor;
        color: $white;
        border-color: $themeColor;
        text-transform: uppercase;
        font-size: 14px; } } } }

.speaker {
  position: relative;
  padding: 19px;


  &:hover {
    .activity-holder {
      opacity: 1;
      visibility: visible; } }

  &:active {
    .activity-holder {
      opacity: 1;
      visibility: visible; } }

  &:hover {
    &:after {
      opacity: 0.04; } }

  @include xs {
    padding: 10px; }

  &:after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    background: #fff;
    z-index: -1;
    opacity: 0;
 }    // transition: all ease 0.2s // removed transition because its somehow very slow in Safari on Mac

  &__photo {
    width: 100%;
    display: flex;
    flex-direction: column;
    img {
      position: relative;
      left: 0;
      width: 100%;
      height: auto;
      object-fit: cover; } }

  &__popup-btn-wrap {
    width: 100%;
    padding: 100% 0 0;
    position: relative; }

  &__popup-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    position: absolute;
    left: 50%;
    bottom: 0;
    font-weight: 500;
    transform: translate(-50%, 50%);
    width: 88px;
    height: 88px;
    border-radius: 50%;
    border: 3px solid $themeColor2;
    color: $themeColor2;
    background: rgba(0, 0, 0, 0.5);
    transition: all ease 0.2s;
    box-shadow: 0 0 0 0 #fff; }

  &__body {
    position: relative;
    max-width: calc(100% - 9px);
    margin-left: auto;
    margin-top: -9px;

    &:before {
      position: absolute;
      content: '';
      display: block;
      width: 100%;
      left: -21px;
      border: 4px solid #fff;
      top: 57px;
      height: 100%;
      top: 10px;
      left: -9px;
      background-color: transparent;
      z-index: 1; } }

  &__inner {
    width: 100%;
    border: 4px solid #fff;
    padding: 18px 5px 20px 16px;
    background-color: #fff;
    z-index: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: $white; }

  &__tag {
    position: absolute;
    right: 29px;
    top: 19px;
    background: var(--tagBG);
    padding: 4px 5px;
    font-size: 14px;
    line-height: 1.8;

    @include xs {
      top: 3px;
      right: 10px;
      font-size: 12px;
      padding: 2px 5px; }

    &:after,
    &:before {
      content: '';
      position: absolute;
      width: 10px;
      height: 23px;
      background: red;
      background-color: var(--tagBG);

      @include xs {
        height: 18px;
        width: 7px; } }


    &:before {
      left: -10px;
      top: auto;
      bottom: 0;

      @include xs {
        left: -7px; } }

    &:after {
      top: 0;
      right: -10px;

      @include xs {
        right: -7px; } } }

  &__expertise {
    margin: 12px 0 12px;
    position: relative;
    line-height: 1.62; }

  &__name {
    font-size: 22px;
    font-weight: 700;
    text-transform: uppercase;
    color: $themeColor;
    margin-bottom: 11px;
    letter-spacing: 0.65px;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.2;
    @include xs {
      font-size: 20px; }

    a {
      color: $white; } }

  &__talk {
    font-size: 14px;
    margin: 0 0 12px;
    color: $gray;

    span {
      font-weight: 600; } }

  &__company {
    color: $gray;
    margin-bottom: 22px;
    @include xs {
      font-size: 14px;
      margin-bottom: 20px; } }
  &__desc {
    color: $gray;
    font-size: 12px;
    line-height: 1.68;

    p {
      word-wrap: break-word;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;

      &:not(:first-child) {
        display: none; } }

    @include sm {
      color: #aaaaaa;
      font-size: 13px; }
    @include xs {
      font-size: 12px;
      margin-bottom: 21px; }
    a {
      color: #bdbdbd;
      text-decoration: none;
      &:hover {
        text-decoration: underline; } }
    &:not(:last-child) {
      margin-bottom: 14px;
      @include xs {
        margin-bottom: 14px; } } }


  &__socials {
    z-index: 2;
    position: relative;
    display: flex; }
  &__social-link {
    &:not(:last-child) {
      margin-right: 16px; }
    .icon {
      transition: 0.3s;
      font-size: 18px;
      fill: #aaaaaa; }
    .no-touch &:hover {
      .icon {
        fill: $themeColor; } } }
  &--mc {
    .speaker {
      &__photo {
        width: 179px;
        padding-top: 179px;
        margin: 0 auto 59px;
        &:after {
          pointer-events: none;
          content: '';
          position: absolute;
          top: 49%;
          left: 50%;
          transform: translate(-50%, -50%);
          width: 235px;
          height: 235px;
          background-size: contain;
          background-position: center;
          background-repeat: no-repeat;
          background-image: url(../2022/img/mc-decor.png); } }
      &__photo-wrap {
        border-radius: 50%;
        @include coverdiv;
        overflow: hidden; } } }


  .activity-holder {
    position: absolute;
    transition: top 150ms ease;
    top: 19px;
    left: 19px;
    right: 19px;
    bottom: 9px;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    visibility: hidden;
    transition: all ease 0.2s;
    cursor: pointer;
    z-index: 2;
    clip-path: polygon(0 0, 100% 0, 100% calc(100% - 10px), calc(100% - 9px) calc(100% - 10px), calc(100% - 9px) 100%, 0 100%);

    @include xs {
      top: 10px;
      left: 10px;
      right: 10px;
      bottom: 0px;
      padding: 15px; }


    a {
      color: $white;
      opacity: 0.7;
      margin: 18px 0 0;
      display: inline-block;
      text-decoration: none;

      &:hover {
        opacity: 1; } }

    .slider {
      position: relative;
      width: auto;
      height: 100%;
      z-index: 1;
      .s-item__col {
        text-align: left;
        width: unset;
        padding: 0;
        padding-left: 0;
        padding-right: 0;
        @include sm {
          font-size: 12px;
          padding-top: 0; } }
      .s-item__text {
        font-size: 12px;
        line-height: 1.3; }
      .slider__title {
        font-size: 16px;
        margin-bottom: 20px;
        min-height: 28px;
        display: none;
        @include sm {
          font-size: 12px;
          margin-bottom: 4px;
          min-height: unset; } }
      .s-item__title {
        @include sm {
          font-size: 14px; } }

      .icon-lightning {
        margin: 0px 0 -3px;
        fill: #888;
        width: 15px;
        height: 15px; } } } }



.hosts {
  position: relative;
  padding: 80px 0 80px;
  background-position: center top;
  background-repeat: repeat;
  background-image: url(../2022/img/bg-net.svg);
  border-bottom: 2px solid #1d201e;

  @include xs {
    padding: 40px 0 40px;
 } }    //margin-top: 20px


.mc {
  position: relative;
  padding: 102px 0 122px;
  background-position: center top;
  background-repeat: repeat;
  background-image: url(../2022/img/bg-net.svg);
  border-bottom: 2px solid #1d201e;
  background: #20201e;

  @include xs {
    padding: 37px 0 55px;
 }    //margin-top: 20px

  .speakers__title {
    color: $white; } }

.mc-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 -19px;
  @include xs {
    padding-top: 20px; }
  &__item {
    position: relative;

    @include xs {
      margin: 0 auto; }
    &:not(:last-child) {
      margin-right: 45px;
      @include sm {
        margin-right: auto;
        margin-bottom: 79px; } } }

  .speaker {

    &__desc {}

    p {
      overflow: visible;
      text-overflow: ellipsis;
      display: block;
      -webkit-line-clamp: none;
      -webkit-box-orient: vertical;

      &:not(:first-child) {
        display: none; } } } }


