	&__content {
		@include r(659) {
			width: 100%; } }
	&__title {
		margin-bottom: 77px;
		@include tablet {
			margin-bottom: 38px; }
		@include md {
			margin-bottom: 60px; }
		@include sm {
			margin-bottom: 40px; }
		@include r(659) {
			//margin-bottom: 40px
			padding: 0 14px; } }

	&__row {
		display: flex;

		@include sm {
			flex-direction: column; } }

	&__left {
		width: 100%;
		max-width: 56%;

		@include sm {
			max-width: 100%; } }

	&__right {
		width: 100%;
		max-width: 44%;
		padding: 0 11%;

		@include tablet {
			padding: 0 0 0 11%; }

		@include sm {
			max-width: 100%;
			padding: 0;
			text-align: center;

			.socials {
				justify-content: center; } } }

	&__descr {
		&:not(:last-child) {
			margin-bottom: 50px;
			@include tablet {
				margin-bottom: 33px; }
			@include md {
				margin-bottom: 30px; }
			@include sm {
				margin-bottom: 24px; }
			@include r(659) {
				//margin-bottom: 24px
				padding: 0 14px; } } }
	&__img {
		margin: 10px 0 80px;

		@include tablet {
			margin: 10px 0 60px; }

		@include sm {
			margin: 40px 0 20px; } }

	&__form {
		max-width: 718px;
		@include r(659) {
			padding: 0 14px; } }
	&__socials {
		@include r(659) {
			padding: 0 14px; }
		&:not(:last-child) {
			margin-bottom: 50px;
			@include tablet {
				margin-bottom: 42px; }
			@include md {
				margin-bottom: 60px; }
			@include sm {
				margin-bottom: 25px; }
			//+r(659)
 }			//argin-bottom: 25px
		&-title {
			font-size: 18px;
			margin: 10px 0 20px; } } }


.form {
	padding-left: 0;
	&__field {
		width: 100%;
		background-color: $white;
		position: relative;
		border: 4px solid $white;
		display: flex;
		&:before {
			position: absolute;
			content: '';
			display: block;
			width: calc(100% + 8px);
			height: calc(100% + 8px);
			border-left: 4px solid $white;
			border-bottom: 4px solid $white;
			top: 4px;
			left: -12px;
			pointer-events: none; } }
	&__input {
		position: relative;
		width: 100%;
		input {
			width: 100%;
			color: $themeColor;
			@include placeholder-color($themeColor);

			@include got;
			font-size: 18px;
			border: none;
			-webkit-appearance: none;
			padding: 20px 26px 18px;
			@include tablet {
				font-size: 18px;
				padding: 19px 16px 16px; }
			@include md {
				font-size: 16px;
				padding: 11px 11px 13px; } } }
	&__btn {
		flex-shrink: 0;
		position: absolute;
		right: 4px;
		top: 2px;
		height: calc(100% - 4px);
		display: flex;
		.btn {
			@include gotM;
			padding: 0;
			padding-left: 22px;
			padding-right: 22px;
			font-size: 16px;
			background: transparent;
			border: none;
			&:not(.btn_border) {
				@include hover {
					transform: none;
					.btn__text {
						text-decoration: underline; } } }
			@include tablet {
				font-size: 14px; }
			@include md {
				font-size: 14px;
				padding: 10px; } } } }
