.event {
	padding: 142px 0 52px;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	background-color: $themeColor;
	position: relative;
	overflow: hidden;
	z-index: 1;

	// &:after
	//osition: absolute
	//ontent: ''
	//isplay: block
	//idth: 100%
	//eight: 100%
	//op: 0
	//eft: 0
	//ackground-color: rgba(0, 0, 0, 0.3)
	//include xs
	//background: #ff2407 !important
	@include md {
		padding: 48px 0 53px; }
	//+sm
	//adding: 30px 0 38px
	// +tablet
	//adding: 85px 0 125px
	@include md {
		padding: 75px 0 87px; }
	@include xs {
		padding: 32px 0 42px; }
	&__video {
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		min-width: 100%;
		min-height: 100%;
		top: 0;
		left: 0;
		object-fit: cover; }
	&__inner {
		display: flex;
		justify-content: space-between;
		max-width: 1284px + 60px*2;
		margin: 0 auto;
		padding: 0 60px;
		position: relative;
		z-index: 2;
		@include tablet {
			padding: 0 60px;
			display: block; }
		@include md;
		@include sm {
			padding: 0 10vw;
			flex-direction: column; }
		@include xxs {
			padding: 0 14px; } }

	&__content {
		padding-top: 20px;
		width: 100%;
		display: flex;
		margin-bottom: 60px;
		// +tablet
		//argin-right: 0
		//adding-top: 0
		//argin-bottom: 40px
		@include md;
		@include sm {
			max-width: 100%;
			margin-bottom: 20px;
			margin-bottom: 0;
			flex-direction: column; }

		a {
			color: $white; } }

	&__left {
		max-width: 55%;
		width: 100%;
		@include sm {
			max-width: 100%; } }

	&__right {
		max-width: 45%;
		padding-left: 5%;
		width: 100%;
		text-align: left;
		@include sm {
			max-width: 100%;
			padding: 0;
			margin: 40px 0 0; } }

	&__info {
		flex-shrink: 0;
		// +tablet
		/// display: flex
		/// flex-wrap: wrap
		@include md;
		@include sm {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			padding-top: 50px; }
		@include xs {
			display: flex;
			flex-wrap: wrap;
			padding-top: 0;
			margin: 0 -10px; } }
	&__title {
		&:not(:last-child) {
			margin-bottom: 78px;
			@include tablet {
				margin-bottom: 38px; }
			@include md {
				margin-bottom: 32px; } } }
	&__descr {}
	&__item {

		&:not(:last-child) {
			margin-bottom: 30px;
			@include md {
				margin: 0 0 22px; }
			@include sm {
				margin-bottom: 12px;
				margin: 0 10px 18px; }
			@include xs {
				width: 43%; } }
		&_lg {
			@include sm {
				margin: 0 10px 18px; } }
		@include xs {
			margin-bottom: 10px;
			br {
				display: none; } } } }




