.section {

	&__title {
		margin-bottom: 63px;
		letter-spacing: 0.6px;
		font-weight: 500;
		font-size: 60px;
		letter-spacing: 0.4px;
		line-height: 1.07;

		.green {
			color: $themeColor; }

		@include tablet {
			margin-bottom: 37px;
			font-size: 48px; }

		@include md {
			margin-bottom: 32px;
			font-size: 32px; }

		&_big {
			font-size: 48px;
			font-weight: 700;
			text-transform: uppercase; } }

	&__subtitle {
		font-weight: 400;
		font-size: 18px;
		line-height: 1.6;
		margin: 30px auto 40px;

		a {
			color: $themeColor;

			&:hover {
				text-decoration: none; } }

		@include tablet {
			font-size: 16px;
			margin: 30px auto 30px; } }

	&__text {
		max-width: 950px;
		margin: 0 auto;
		@include tablet {
			max-width: 870px; } } }
