.h-list {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	@include md {
		margin: 0 auto; }
	@include sm {
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center; }

	&__item {
		display: flex;
		justify-content: flex-end;

		&:not(:last-child) {
			margin-bottom: 30px;
			@include md {
				margin: 0 0 22px; }
			@include sm {
				margin-bottom: 12px;
				margin: 0 10px 18px; } } }

	&__icon {
		font-size: 82px;

		@include tablet;

		@include sm {
			font-size: 51px;
			display: flex; }

		.icon {
			fill: $themeColor2; }
		&:not(:last-child) {
			margin-right: 30px;
			@include md {
				margin-right: 19px; } } }

	&__value {
		@include gotL;
		font-size: 88px;
		line-height: 1;
		text-align: right;
		padding: 0 24px 0 0;

		@include md {
			font-size: 74px;
			line-height: 1.05;
			padding: 0 16px 0 0;
			min-width: 180px; }

		@include sm {
			font-size: 72px;
			min-width: 0; }
		@include xs {
			font-size: 62px;
			padding-right: 10px; } }

	&__text {
		display: block;
		padding-top: 8px;
		min-width: 120px;
		@include gotL;
		font-size: 14px;
		line-height: 1.48;
		@include md {
			font-size: 12px; }
		@include sm {
			font-size: 14px;
			line-height: 1.3;
			padding-top: 0;
			margin-top: 5px; }
		@include xs {
			margin-top: 8px;
			min-width: 80px;
			font-size: 12px;
			br {
				display: block; } } }

	&__text-top {
		@include gotL;
		display: inline-block;
		font-size: 18px;
		letter-spacing: 0.11px;
		line-height: 1;
		margin: 0 0 12px;
		@include md {
			margin: 0 0 8px;
			font-size: 16px; }
		@include sm {
			font-size: 20px;
			margin: 0 0 4px; }
		@include xs {
			font-size: 14px; } } }




.info-container {
	padding: 42px 0 24px;
	@include md {
		padding: 24px 0; }
	&__inner {
		display: flex;
		justify-content: center;
		@include xs {
			flex-direction: column;
			align-items: center; }
		.info {
			&:not(:last-child) {
				margin-right: 106px;
				@include sm {
					margin-right: 50px; }
				@include xs {
					margin: 0 0 20px; } } } } }
