.location {
	padding: 163px 0;
	position: relative;

	@include tablet {
		padding: 113px 0 124px; }
	@include md {
		padding: 75px 0 50px; }
	@include xs {
		padding-bottom: 0; }

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, .5);
		z-index: 2; }

	> .container {
		position: relative;
		z-index: 2;
		@include md {
			padding: 0; } }

	&__inner {
		display: flex;
		justify-content: space-between;
		@include md {
			display: block; } }
	&__content-wrap {
		width: 100%;
		max-width: 580px;
		display: flex;
		@include md {
			max-width: 100%;
			padding: 0 10vw;
			min-height: 250px; }
		@include xxs {
			padding: 0 14px; } }
	&__content {
		max-width: 580px;
		flex-direction: column;
		justify-content: space-between;
		display: none;
		&.is-active {
			display: flex; }

		@include md {
			max-width: 100%;
			flex-direction: column;
			justify-content: unset;
			margin-bottom: 39px; } }
	&__title {
		margin-bottom: 80px;
		@include tablet {
			margin-bottom: 32px; }
		@include md {
			margin-bottom: 37px;
			padding: 0 10vw; }
		@include xxs {
			padding: 0 14px; } }
	&__subtitle {
		@include gotM;
		font-size: 44px;
		letter-spacing: 0.26px;
		padding-top: 12px;
		display: none;
		&.is-active {
			display: block; }
		@include tablet {
			font-size: 36px; }
		@include md {
			font-size: 32px; } }
	&__top {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 54px;
		@include tablet {
			margin-bottom: 35px; }
		@include md {
			padding: 0 10vw;
			flex-direction: column; }
		@include sm {
			margin-bottom: 20px; }
		@include xxs {
			padding: 0 14px; } }
	&__nav {
		display: flex;
		padding-left: 30px;
		max-width: 640px;
		width: 100%;
		@include tablet {
			padding-right: 7px;
			max-width: 406px; }
		@include md {
			padding: 10px 0 0;
			width: auto; }
		@include sm {
			justify-content: center;
			padding-right: 0;
			margin-bottom: 20px; }
		.swiper-button {
			&:last-child {
				top: 10px; } }

		.swiper-button-disabled {
			opacity: 0.3; } }
	&__descr {
		@include tablet {
 }			// margin-bottom: 70px
		@include md {
			margin-bottom: 29px; } }
	&__footer {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-start;
		font-size: 22px;
		line-height: 1.6;
		@include got;
		padding-right: 15px;
		@include tablet {
			font-size: 18px;
			line-height: 1.5;
			padding-right: 0; }
		@include md {
			font-size: 16px;
			margin-top: auto; }
		& p {
			width: 100%;
			display: flex;
			justify-content: space-between;
			@include tablet {
				flex-direction: column; }

			& a {
				color: $themeColor2;
				font-size: 20px;
				text-decoration: underline;
				@include hover {
					text-decoration: none; }
				@include tablet {
					font-size: 18px; } } } }
	&__link {
		color: $themeColor2;
		font-size: 20px;
		text-decoration: underline;
		@include hover {
			text-decoration: none; }
		@include tablet {
			display: none; } }
	&__map {
		width: 100%;
		max-width: 641px;
		flex-shrink: 0;
		padding: 9px 0 3px 30px;
		@include tablet {
			max-width: 407px; }
		@include md {
			max-width: 100%;
			padding: 9px 10vw 3px; }
		@include sm {
			padding: 0 10vw; }
		@include xs {
			padding: 0; } }
	&__slider {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%; } }
.map {
	width: 100%;
	height: 419px;

	@include tablet {
		padding-right: 7px;
		height: 269px; }
	@include md {
		padding-right: 0;
		height: 241px; }
	&__inner {
		border: 4px solid $white;
		height: 100%;
		position: relative;
		//&:before, &:after
		//osition: absolute
		//ontent: ''
		//isplay: block
		//idth: calc(100% + 8px)
		//eight: calc(100% + 9px)
		//order: 4px solid $white
		//include md
		//display: none
		&:before {
			top: 5px;
			left: 7px; }
		&:after {
			top: 15px;
			left: 17px; } }
	&__decor {
		position: absolute;
		width: calc(100% + 8px);
		height: calc(100% + 9px);
		border-left: 4px solid $white;
		border-bottom: 4px solid $white;
		top: 6px;
		right: 6px;
		&:before, &:after {
			position: absolute;
			content: '';
			display: block;
			background-color: $white; }
		&:before {
			height: 4px;
			width: 8px;
			top: 0;
			left: 0; }
		&:after {
			height: 8px;
			width: 4px;
			bottom: 0;
			right: 0; }

		@include md {
			display: none; }
		&_2 {
			top: 16px;
			right: 16px; }
		// &_3
		// 	top: 54px
		// 	left: 57px
		// 	&:before
		// 		width: 38px
		// 	&:after
		// 		height: 38px
		// &_4
		// 	top: 65px
 }		// 	left: 67px
	&__content {
		height: 100%;
		position: relative;
		z-index: 2; }

	&__pins {
		display: none; }

	&__image {
		width: 100%;
		height: 100%;
		display: block;
		object-fit: none;

		@include md {
			object-fit: cover; } } }

.slider {
	height: 100%; }
.swiper-button-prev, .swiper-button-next {
	width: 66px;
	height: 66px;
	background-color: transparent;
	font-size: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	top: auto;
	right: auto;
	bottom: auto;
	left: auto;
	transition: background .3s ease;
	background: $white;

	@include tablet {
		width: 50px;
		height: 50px; }
	@include md {
		width: 40px;
		height: 40px; }
	.icon {
		fill: $themeColor2; }

	.swiper-button-icon {
		fill: #000; }

	&:hover {
		background: $themeColor2; } }


.slide {
	&__inner {
		height: 100%;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat; } }

.gm-style-cc {
	display: none; }
