.zoom-bars {
	padding: 120px 0 100px;
	color: $black;
	@include md {
		padding: 80px 0 50px; }

	.section {
		&__title {
			color: $themeColor; } } }


.bars-list {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	margin: 70px 0 50px;
	color: $white;
	// gap: 20px

	@media all and (max-width: 1300px) {
		margin: 70px -20px 50px; }

	@include md {
		margin: 40px -20px 30px; }

	@include sm {
		margin: 40px -20px 30px; }

	&__item {
		position: relative;
		width: calc(100% / 3 - 16px);
		padding: 42px 30px 44px;
		font-size: 20px;
		overflow: hidden;

		&.no-photo {
			min-height: 500px;

			@include tablet {
				min-height: 400px; }

			@include xs {
				min-height: 300px; }

			.bars-list__name {
				margin-top: auto; }

			&:hover {
				.bars-list__bg {
					transform: scale(1.1); } } }



		@include md {
			width: calc(100% / 2 - 16px);
			font-size: 3vw; }

		@include xs {
			width: 100%; }

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.5); } }

	&__link {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: 2;
		cursor: pointer; }

	&__bg {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		transition: all ease 0.2s; }

	&__content {
		position: relative;
		display: flex;
		flex-direction: column;
		text-align: center;
		align-items: center;
		z-index: 1;
		height: 100%; }

	&__bar {
		font-size: 50px;
		display: flex;
		line-height: 1;
		font-weight: 700;
		text-transform: uppercase;
		text-align: center;
		margin: 0 0 20px;
		font-size: 40px;
		min-height: 120px;
		align-items: center;

		@include tablet {
			font-size: clamp(20px, 4vw, 26px);
			min-height: clamp(60px, 8vw, 78px); } }

	&__with {
		display: none; }

	&__name {
		font-size: clamp(14px, 1.8vw, 20px);
		line-height: 1.2;
		margin-bottom: 26px;

		a {
			color: $themeColor2; }

		@include tablet {
			font-size: clamp(14px, 3vw, 16px); } }

	&__rooms {
		display: flex;
		margin: auto 0 0; }

	&__room {
		position: relative;
		border: 13px solid #fff;
		border-radius: 50%;
		overflow: hidden;
		flex: 0 0 auto;
		background: #fff;
		cursor: pointer;

		@include tablet {
			border-width: 10px; }

		&+.bars-list__room {
			margin: 0 0 0 -62px;

			@include sm {
				margin: 0 0 0 -5vw; }

			@include xs {
				margin: 0 0 0 -10vw; }

			@include xxs {
				margin: 0 0 0 -15vw; } }

		&:after {
			font-size: 14px;
			font-weight: bold;
			text-transform: uppercase;
			content: 'JOIN';
			position: absolute;
			display: flex;
			align-items: center;
			justify-content: center;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.5);
			opacity: 0;
			transition: all ease 0.1s;
			color: $themeColor2; }

		&:hover {
			border-color: $themeColor2;

			&:after {
				opacity: 1; } } }

	&__photo {
		display: block;
		width: 175px;
		height: 175px;
		max-width: 170px;
		max-height: 170px;

		@include tablet {
			width: 12vw;
			height: 12vw; }

		@include sm {
			width: 20vw;
			height: 20vw; }

		@include xs {
			width: 30vw;
			height: 30vw; }

		@include xxs {
			width: 45vw;
			height: 45vw; } } }
