@keyframes type__animation-cursor {
	0% {
		opacity: 1; }
	50% {
		opacity: 1; }
	51% {
		opacity: 0; }
	100% {
		opacity: 0; } }

.hero {
	//min-height: 1033px
	min-height: 100vh;
	padding: 156px 58px 120px;
	display: flex;
	flex-direction: column;
	position: relative;

	@media screen and (min-width: 1024px) and (max-height: 800px) {
		padding: 90px 58px 60px; }

	@include tablet {
		// padding: 90px 58px 60px
		padding: 156px 58px 120px; }

	@include md {
		// padding: 45px 30px 45px
		padding: 90px 58px 60px; }
	@include sm {
		padding: 45px 40px 15px; }
	@include xs {
		padding: 15px 15px 15px; }


	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: url(../2022/img/hero-overlay.svg) center top repeat;
		background-color: rgba(0, 0, 0, .3);
		z-index: 1;

		@include sm {
			background: url(../2022/img/overlay_small.svg);
			background-color: rgba(0, 0, 0, .3); } }

	.type__animation-cursor {
		width: 0.05em;
		min-width: 2px;
		height: 1em;
		background-color: #fff;
		display: none;
		position: relative;
		top: 0.1em;
		left: -0.25em;
		animation: type__animation-cursor 700ms infinite;
		@include sm {
			display: inline-block; } }

	&_live {
		align-items: flex-start;
		height: 130px;
		min-height: 0;
		padding: 20px 0 20px;

		@include tablet {
			padding: 20px 0 20px; }
		@include md {
			padding: 20px 0;
			height: 120px; }
		@include sm {
			padding: 10px 0; }
		@include xs {
			height: auto;
			padding: 10px 0 !important; }


		.container {
			position: relative;
			z-index: 2;
			pointer-events: auto;
			width: 100%;
			height: 100%;
			padding: 0 80px;

			@include tablet {
				padding: 0 20px; }


			@include sm {
				padding: 0 20px; } }

		.hero {
			&__bg {
				position: absolute;
				left: 0;
				top: 0;
				min-width: 100%;
				min-height: 100%;
				width: auto;
				height: auto;
				z-index: -1;
				background: url(../2022/img/coc.jpg) center top no-repeat;
				background-size: cover; }

			&__inner {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				display: flex;
				justify-content: space-between;
				flex-direction: row;

				@include md {
					margin: 0; }

				@include xs {
					flex-direction: column-reverse;
					align-items: center; } }

			&__date {
				font-size: 18px;
				margin: auto 0;
				text-align: left;

				@include md {
					font-size: 14px; }

				@include xs {
					font-size: 12px;
					margin: 15px 0 10px; } }

			&__logo {
				width: 200px;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				margin: 0;

				@include tablet {
					width: 160px;
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					margin: auto; }


				@include md {
					width: 150px; }

				@include xs {
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					width: 130px;
					margin: 0px auto 0; } }

			&__partner {
				position: relative;
				height: auto;
				margin: 10px 0 auto 0;
				top: 0;
				right: 0;
				font-size: 14px;

				img {
						display: block; }

				@include tablet {
					font-size: 10px;
					margin: 10px 0 auto 0; }

				@include md {

					img {
						height: 58px; } }

				@include sm {
					margin: 20px 0 auto 10px;

					img {
						height: 55px; } }

				@include xs {
					display: none; }

				img {
					height: 65px; } }

			&__partners {
				display: flex;
				@include md {
					margin: 0; } }



			&__partner-text {
				@include sm {
					display: none; } } } }



	&__inner {
		position: relative;
		max-width: 1280px;
		margin: 0 auto;
		width: 100%;
		display: flex;
		flex-direction: column;
		flex: 1;
		z-index: 2; }

	&__logo {
		margin-bottom: 0;
		display: flex;
		justify-content: space-between;
		z-index: 2;

		@include md {
			margin: 23px 0 46px; }
		@include sm {
 }			// margin-bottom: 57px
		@include xs {
			margin: 0 0 0;
			flex-direction: column; } }

	&__date {
		font-size: 22px;
		font-weight: 700;
		margin: auto 0 16px 20px;
		letter-spacing: 0.05em;
		text-align: right;

		@include md {
			margin: auto 0 10px 20px; }

		@include sm {
			margin: auto 0 6px 20px;
			font-size: 18px; }

		@include xs {
			margin: 30px auto 20px 0;
			padding-left: 0;
			font-size: 16px;
			font-weight: 500;
			text-align: left;
			letter-spacing: 0;

			br {
				display: none; } } }

	&__content {
		display: flex;
		justify-content: space-between;
		margin: auto 0;

		@include md {
			margin: 0;
			margin: auto 0;
			position: relative;
			top: -36px; }

		@include sm {
			margin: auto 0;
			flex-direction: column;
			top: 0; } }

	&__left {
		text-align: left;
		max-width: 640px;
		margin-right: 40px;

		@include sm {
			text-align: center;
			max-width: none;
			margin-right: 0; }
		@include xs {
			text-align: left; } }

	&__right {
		flex: 0 0 auto;
		@include md; }

	&__bottom {
		display: flex;
		justify-content: space-between;

		@include md {
			margin: auto 0 0; }

		@include xs {
			flex-direction: column-reverse;
			justify-content: center; }

		.socials {
			margin: 26px 20px 0 0;
			@include xs {
				margin: 0; } } }

	&__title {
		@include gotM;
		margin: 0 0 50px;
		font-size: 48px;
		line-height: 1.26;
		letter-spacing: -0.005em;
		display: block;
		padding: 0 0 0 102px;
		z-index: 2;

		@media screen and (max-width: 1100px) {
			padding-left: 50px; }

		@include md {
			font-size: 40px;
			padding: 0 0 0 79px;
			margin: 0 0 36px; }

		@include md {
			// font-size: 34px
 }			// padding: 0 0 0 30px

		@include sm {
			font-size: 34px;
			margin-bottom: 16px;
			padding-left: 0; }

		@include xs {
			font-size: 24px;
			margin-bottom: 36px;
			line-height: 1.34;
			padding-left: 38px; } }

	&__subtitle {
		@include gotM;
		margin: 44px 0 60px;
		font-size: 35px;
		font-weight: 400;
		line-height: 1.26;
		letter-spacing: -0.005em;
		display: block;
		z-index: 2;
		text-align: center;

		@include md {
			font-size: 32px; }

		@include sm {
			font-size: 22px;
			margin: 32px 0 40px; }

		@include xs {
			margin: 26px 0 32px; } }

	&__descr {
		@include got;
		font-size: 18px;
		line-height: 2;
		margin-bottom: 28px;
		@include md {
			font-size: 14px;
			max-width: 500px; }

		@include sm {
			font-size: 16px;
			line-height: 1.6;
			margin-bottom: 15px;
			max-width: none; }

		@include xs {
			font-size: 14px;
			line-height: 1.76;
			margin-bottom: 34px; } }


	&__techs-title {
		font-size: 18px;
		line-height: 1.6;
		margin-bottom: 63px;
		padding-top: 20px;

		a {
			color: $white;

			&:hover {
				text-decoration: none; } }

		@include md {
			font-size: 18px;
			line-height: 1.5;
			margin-bottom: 68px;
			padding-top: 10px; }
		@include sm {
			text-align: center;
			font-size: 14px;
			line-height: 1.6;
			margin-bottom: 42px; } }


	&__btn {
		display: flex;
		flex-wrap: wrap;
		gap: 10px;
		.btn {
			height: 66px;
			padding: 0 30px;
			@include gotM; }
		@include sm {
			//padding: 0 10px
			.btn {
				min-width: 247px;
				width: 100%; } }
		@include xs {
			margin: 40px 0 15px; }
		&-tip {
			margin: 20px 0 0;
			font-size: 12px;
			@include sm {
				margin: 10px 0 20px; }
			@include xs {
				text-align: center; } } }

	&_min {
		min-height: auto;
		padding: 110px 0 100px;
		background-position: center;
		background-size: cover;

		&:after {
			display: none; }
		@include md {
			padding: 94px 20px; }
		@include sm {
			padding: 44px 20px; }
		@include xs {
			padding: 44px 20px; }
		.hero {
			&__logo {
				margin: 46px auto 68px;
				@include md {
					margin-bottom: 20px; }
				.logo {
					max-width: 302px;
					@include sm {
						max-width: 220px; } } }
			&__title {
				font-size: 72px;
				max-width: 1280px;
				text-align: center;
				padding: 0;
				margin: 0 auto;
				line-height: 1.1;
				@include tablet {
					font-size: 60px; }
				@include md {
					font-size: 44px; }
				@include sm {
					font-size: 32px; } } } }

	&__video {
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: 0;
		object-fit: cover; }

	&_attendees {
		min-height: 0;
		padding: 45px 20px;
		background-size: cover;
		background-position: center center;

		@include tablet {
			padding: 45px 20px; }
		@include md {
			padding: 45px 20px; }
		@include sm {
			padding: 25px 20px; }
		@include xs {
			padding: 25px 20px; }


		.hero {

			&__inner {
				width: 100%;
				text-align: center; }

			&__logo {
				width: 200px;
				margin-bottom: 40px;
				margin-left: auto;
				margin-right: auto;

				@include md {
					width: 150px;
					margin-top: 0; }

				@include xs {
					width: 130px; } }

			&__title {
				text-align: center;
				font-size: clamp(38px, 6vw, 68px);
				line-height: 0.9;
				text-align: center;
				margin-bottom: 38px;
				font-weight: 900;
				padding: 0; }

			&__suptitle {
				margin-bottom: 10px; }

			&__btn {
				margin: 20px 0 0; } } } }





.intro {
	z-index: 1;
	position: relative;
	overflow: hidden; }
