.footer {
	color: $themeColor;
	padding: 119px 0 0;
	background: #20201e;
	@include tablet {
		padding: 73px 0 0; }
	@include md {
		padding: 40px 0 0; }
	&__inner {
		display: flex;
		justify-content: space-between;
		max-width: 1220px;
		padding-bottom: 108px;
		@include tablet {
			max-width: 790px;
			padding-bottom: 95px; }
		@include sm {
			flex-direction: column-reverse;
			padding-bottom: 78px;
			align-items: center; } }
	&__content {
		max-width: 600px;
		width: 100%;
		padding-top: 85px;
		@include tablet {
			padding-top: 40px; }
		@include md {
			padding-top: 0; } }
	&__socials {
		max-width: 569px;
		margin: auto 0;
		width: 100%;
		@include tablet {
			max-width: 365px;
			flex-shrink: 0; }
		@include sm {
			margin: 0 auto 25px; }

		.socials {
			&__item {
				@include xs {
					margin: 0 10px!important; } }

			&__link {
				@include xs {
					width: 35px;
					height: 27px; } }

			.icon {
				fill: $themeColor; } }

		.socials-btns {
			width: 100%;
			flex-wrap: wrap;
			justify-content: center;
			&__item {
				margin-right: 0;
				margin-bottom: 58px;
				@include tablet {
					margin-bottom: 38px; }
				@include md {
					margin-bottom: 30px; }
				&:not(:last-child) {
					&:not(:nth-child(3n)) {
						margin-right: 88px;
						@include tablet {
							margin-right: 48px; }
						@include md {
							margin-right: 24px; } } } } } }
	&__title {
		max-width: 600px;
		@include md {
			margin-bottom: 37px; } }
	&__nav {
		li {
			&:not(:last-child) {
				margin-bottom: 16px;
				@include tablet {
					margin-bottom: 12px; }
				@include md {
					margin-bottom: 14px; } }
			a {
				color: $white;
				font-size: 18px;
				line-height: 1.2;
				text-decoration: underline;
				@include hover {
					text-decoration: none; }
				@include tablet {
					font-size: 16px; } } } }
	&__bot {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 92px 0 87px;
		background-size: cover;
		background-position: left top;
		background-repeat: repeat-x;
		animation: translateBG 50s linear infinite;
		@include tablet {
			padding: 81px 0 74px; }
		@include md {
			padding: 39px 0 32px; } } }
.footer-logo {
	position: relative;
	color: $white;
	display: flex;
	align-items: center;
	height: 92px;
	width: 100%;
	@include xs {
		width: auto; }

	&__subtitle {
		position: absolute;
		right: 50%;
		font-size: 18px;
		margin-top: -13px;
		margin: 0 50px 0 0;
		@include tablet {
			font-size: 14px; }
		@include md {
			font-size: 14px; }
		@include xs {
			position: relative;
			right: auto;
			margin: 0; } }
	&__img {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		width: 71px;
		margin: 0;
		@include tablet {
			width: 63px; }
		@include md {
			width: 57px; }
		@include xs {
			position: relative;
			left: 0;
			top: 0;
			transform: translate(0, 0);
			margin: 0 15px 0 15px; } }
	&__title {
		color: $white;
		font-size: 31px;
		text-decoration: underline;
		@include tablet {
			font-size: 14px; }
		@include md {
			font-size: 24px;
			letter-spacing: -2px; } }

	&__text {
		position: absolute;
		left: 50%;
		margin: 0 0 0 50px;
		width: 142px;
		position: relative;
		padding-bottom: 4px;
		&:after {
			position: absolute;
			content: '';
			display: block;
			width: 100%;
			height: 4px;
			background-color: $white;
			bottom: 0;
			left: 0; }
		@include hover {
			&:after {
				display: none; } }
		@include tablet {
			width: 120px; }
		@include sm {
			width: 111px; }
		svg {
			fill: $themeColor; }
		@include xs {
			position: relative;
			left: 0;
			margin: 0; } } }

.li-counter {
	position: absolute;
	right: 10px;
	bottom: 10px;
	opacity: 0;
	visibility: hidden; }


@keyframes translateBG {
	0% {
		background-position: 0 0; }
	100% {
		background-position: -1640px 0; } }
