.progress {
	padding: 163px 0 175px;
	@include tablet {
		padding: 115px 0 130px; }
	@include md {
		padding: 77px 0 88px; }
	&__content {
		display: flex;
		margin: 0 0 88px;
		@include sm {
			flex-direction: column;
			margin: 0 0 50px; } }
	&__left {
		width: 100%;
		max-width: 50%;
		@include sm {
			max-width: 100%; } }
	&__right {
		width: 100%;
		max-width: 50%;
		padding-left: 5.5%;
		display: flex;
		flex-direction: column;
		margin: auto 0;
		@include sm {
			margin: 50px 0 0;
			max-width: 100%;
			padding-left: 0; }
		.progress__sponsors {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			.progress__logo {
				width: 100%;
				display: flex;
				flex-direction: column;
				justify-content: center; } } }

	&__title {
		// max-width: 600px
		line-height: 1.3;
		@include md {
			padding-right: 25px; }
		&:not(:last-child) {
			margin-bottom: 70px;
			@include tablet {
				margin-bottom: 37px; }
			@include md {
				margin-bottom: 31px; } } }
	&__descr {
		@include got;
		max-width: 1230px;
		&:not(:last-child) {
			margin-bottom: 55px;
			@include tablet {
				margin-bottom: 50px; }
			@include md {
				margin-bottom: 30px; } } }
	&__img {
		display: block;
		max-width: 256px;
		height: auto;
		margin: 0 auto 30px;
		@include xs {
			max-width: 200px; } }
	&__subtitle {
		text-align: center;
		font-size: 18px;
		@include tablet {
			font-size: 18px; }
		@include md {
			font-size: 16px; }
		&:not(:last-child) {
			margin-bottom: 32px;
			@include tablet {
				margin-bottom: 35px; }
			@include md {
				margin-bottom: 25px; } } }
	&__bar {
		&:not(:last-child) {
			margin-bottom: 100px;
			@include tablet {
				margin-bottom: 70px; }
			@include md {
				margin-bottom: 40px; } } }
	&__btns {
		display: flex;
		justify-content: center;
		@include md {
			flex-direction: column;
			align-items: center;
 }			//padding-left: 10px
		@include sm {
			padding-left: 5px;
			padding-right: 5px; }
		.btn {
			font-size: 16px;
			width: 490px;
			max-width: 100%;
			padding: 22px;
			@include tablet {
				padding-left: 10px;
				padding-right: 10px; }
			@include sm {
				padding: 10px; }
			&:not(.btn_mob-p-s) {
				padding-top: 24px;
				padding-bottom: 24px; }
			&:not(:last-child) {
				margin-right: 20px;
				@include tablet {
					margin-right: 30px; }
				@include md {
					margin-right: 0;
					margin-bottom: 28px; } } } } }

.progress-bar {
	width: 100%;
	height: 5px;
	background: rgba(0, 0, 0, 0.2);

	&__content {
		height: 100%;
		max-width: 100%;
		overflow: hidden;
		background: $themeColor2;
		background-size: auto 100%;
		background-repeat: repeat-x; } }
