.sponsors {
	padding: 165px 0 150px;
	color: $themeColor;
	background: $grayL;
	@include tablet {
		padding: 112px 0 100px; }
	@include md {
		padding: 75px 0 55px; }
	&__title {
		&:not(:last-child) {
			margin-bottom: 78px;
			@include tablet {
				margin-bottom: 39px; }
			@include md {
				margin-bottom: 33px; } } }
	&__descr {
		//max-width: 870px

		a {
			color: $themeColor;
			&:hover {
				text-decoration: none; } }

		&:not(:last-child) {
			margin-bottom: 88px;
			@include tablet {
				margin-bottom: 35px; }
			@include md {
				margin-bottom: 24px; } } }
	&__item {
		display: flex;
		&:not(:last-child) {
			margin-bottom: 58px;
			@include tablet {
				margin-bottom: 39px; }
			@include md {
				margin-bottom: 20px; } } } }


.logos {
	margin-left: 11px;
	width: 100%;
	position: relative;

	@include md {
		margin: 0; }

	&:before {
		position: absolute;
		content: '';
		display: block;
		width: 100%;
		height: 100%;
		border: 4px solid $white;
		background: $grayL;
		top: 10px;
		left: -10px;
		z-index: 0;
		@include md {
			display: none; } }
	&__inner {
		width: 100%;
		min-height: 397px;
		color: $themeColor;
		position: relative;
		background-color: $white;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0 40px 0 80px;
		display: flex;
		flex-direction: column;

		@include tablet {
			min-height: 280px; }
		@include md {
			margin-left: 0;
			padding: 0; }
		@include sm {
			min-height: unset; } }

	&__title {
		@include got;
		font-size: 30px;
		line-height: 1.2;
		letter-spacing: 0.24px;
		padding: 13px 18px 10px;
		flex-shrink: 0;
		@include tablet {
			font-size: 36px;
			padding: 10px 12px; }
		@include md {
			font-size: 32px;
			padding: 5px 13px 0; } }
	&__info {
		//width: 397px - 6px
		height: 73px;
		//border-bottom: 2px solid $themeColor
		//border-right: 2px solid $themeColor
		display: flex;
		justify-content: flex-start;
		position: absolute;
		left: 0;
		top: 100%;
		transform-origin: 0 0;
		transform: translate(0, 0) rotate(-90deg);
		@include tablet {
			height: 57px;
 }			//width: 100%
		@include md {
			position: static;
			transform: none;
			width: auto;
			height: auto;
			border-right: none;
			align-items: center;
			width: 100%;
			padding-bottom: 4px;
			border-bottom: 2px solid $grayL; } }
	&__empty {
		width: 100%;
		position: relative;
		overflow: hidden;
		//&:after
		//osition: absolute
		//ontent: ''
		//isplay: block
		//idth: 150%
		//eight: 1px
		//op: 50%
		//eft: 50%
		//ransform: translate(-50%, -50%) rotate(18deg)
 }		//ackground-color: $themeColor
	&__content {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		width: 100%;
		margin: auto 0;
		@include md {
			padding: 20px 20px; }
		@include xs {
			padding: 20px 0; } }
	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 20px;
		width: 50%;
		transition: all .1s;
		transform: scale(1);

		&:hover {
			// opacity: 0.8
			transform: scale(1.03); }

		img {
			max-width: 100%;
			max-height: 150px; }

		@include tablet {
			padding: 25px 30px; }
		@include md {
			width: 100%;
			padding: 20px 25px;

			img {
				max-height: 120px; } } }
	&_md {
		.logos {
			&__content {
				//+md
 }				//adding-top: 0
			&__link {
				width: 33.33%;

				&.square {
					width: 23%; }

				@include md {
					width: 50%;
					padding: 10px 20px;
					&.square {
						width: 35%; } }
				@include sm {
					padding: 10px 15px; }
				//+sm
				//idth: 100%
 } } }				///padding: 10px 46px
	&_sm {
		.logos {
			&__content {
				padding-top: 0; }
			&__link {
				width: 25%;

				@include md {
					width: 33%;
					padding: 10px 20px; }

				@include sm {
					padding: 10px 15px;
					width: 50%; } } } }

	&_xs {
		.logos {
			&__content {
				//+md
 }				//adding-top: 0
			&__link {
				width: 20%;
				@include md {
					width: 25%;
					padding: 10px 20px; }
				@include sm {
					width: 33.3%;
					padding: 10px; } } } }
	&_xs {
		.logos {

			&__content {
				padding: 20px 0; } }

		img {
			max-height: 100px; } } }
