.s-workshops-list {
  padding: 100px 0;
  background: #20201e;

  .container {
    max-width: 1260px; }

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: clamp(24px, 4vw, 42px);

    @include sm {
      flex-direction: column; }

    .section {

      &__title {
        text-align: left;
        line-height: 1;
        font-weight: 500;
        font-size: clamp(38px, 6vw, 60px);
        margin-bottom: 38px; }

      &__subtitle {
        max-width: none;
        text-align: left;
        line-height: 1.48;
        margin: 24px 0;
        font-weight: 400;
        font-size: clamp(14px, 4vw, 16px); }

      &__info {
        max-width: none;
        text-align: left;
        line-height: 1.48;
        margin: 24px 0;
        font-weight: 400;
        font-size: clamp(14px, 4vw, 16px);
        a {
          color: $themeColor2; }

        p {
          &:not(:first-child) {
            margin: 4px 0 0; } } } } }

  &__desc {
    max-width: 600px;
    width: 100%;
    flex: 1 1 auto; }

  &__img {
    max-width: 490px;
    width: 100%;
    flex: 1 1 auto;
    margin-top: 5px;
    margin-left: 40px;

    @include sm {
      max-width: 520px;
      margin: 0 auto; } } }

.workshops-imgs {
  position: relative;
  width: 100%;
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: 1fr 1fr 1fr;

  &__item {
    position: relative;
    aspect-ratio: 1.77;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block; } } }

.workshops-list {

  &__item {
    display: flex;
    padding: 26px 0;

    &:not(:first-child) {
      border-top: 3px solid #3e3e3b; }

    @include sm {
      flex-direction: column; } }

  &__item-overlay {
    display: flex;
    width: 100%;
    cursor: pointer;

    @include sm {
      flex-direction: column; }

    &:hover {
      .workshops-list__title {
        text-decoration: underline !important; } } }

  &__type {
    flex: 0 0 42px;
    color: $grayD;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 500;

    @include sm {
      flex: 1 1 auto;
      margin: 0 0 10px; } }

  &__desc {
    flex: 1 1;
    padding-right: 20px; }

  &__title {
    font-size: clamp(22px, 2.6vw, 28px);
    font-weight: 500;
    line-height: 1.1;
    color: $white; }

  &__date {
    font-size: 12px;
    margin: 10px 0 0; }

  &__speakers {
    display: flex;
    flex-direction: column;
    flex: 0 0 180px;
    gap: 5px;
    padding-right: 20px;

    @include sm {
      flex-direction: row;
      flex: 1 1 auto;
      margin: 18px 0;
      gap: 20px 20px; } }

  &__speaker {
    display: flex;
    align-items: center; }

  &__speaker-photo {
    width: 40px;
    height: 40px;
    margin: 0 12px 0 0; }

  &__speaker-name {
    font-size: 12px; }

  &__buttons {
    flex: 0 0 150px;
    display: flex;
    flex-direction: column;
    gap: 5px;

    .btn {
      padding: 3px 15px;
      min-height: 40px;

      &:last-child {
        margin-bottom: -10px; } }

    @include sm {
      flex: 1 1 auto;
      flex-direction: row;
      flex: 1 1 auto;
      margin: 0;
      gap: 10px; } } }
