.techs {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0;

	&__item {
		display: flex;
		justify-content: center;
		min-width: 85px;
		margin: 0 0 30px;
		flex-direction: column;

		@include tablet {
			min-width: auto;
			width: 25%; }
		@include sm {
			width: 16.6%; }
		@include xs {
			width: 20%; }
		@include xxxs {
			width: 25%; } }



	&__link {
		text-decoration: none;
		color: #fff;
		transition: color .1s;

		&:hover {
			text-decoration: underline;
			color: $themeColor2; } }

	&__img-box {
		height: 50px;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 0 9px;
		@include xs {
			margin: 0 0 0; }

		img {
			transform: scale(0.80);
			max-height: 58px;

			@include md {
				transform: scale(0.75); } } }


	&__title {
		font-size: 11px;
		text-align: center; } }

